<template>
  <!--微信组件-->
  <div id="wechat">
    <ul class="wechat-list">
      <!--props传递消息对象 baseMsgObj "-->
      <msg-item
        v-for="baseMsgObj in $store.state.msgList.baseMsg"
        :item="baseMsgObj"
        class="list-row line-bottom"
        :key="baseMsgObj.mid"
      ></msg-item>
    </ul>
  </div>
</template>
<script>
import search from "../common/search";
import msgItem from "../wechat/msg-item";
export default {
  components: {
    search,
    msgItem
  },
  mixins: [window.mixin],
  data() {
    return {
      pageName: "消息",
      baseMsg: ""
    };
  },
  updated() {
    // console.log(this.$store.state.msgList.baseMsg);
  },
  methods: {
    findChatList() {
      this.$http.get("/api/chat/findChatList").then(res => {
        // console.log(res);
        if (res.data.code == 200) {
          res.data.data.map(item => {
            if (item.msg[0].msgType == "img") {
              item.msg[0].msg = "[图片]";
            }
          });
          // console.log(res.data.data);
          this.$store.commit("getBaseMsg", res.data.data);
        } else {
          this.$router.push("/login");
          localStorage.setItem("loginFlag", "");
        }
      });
    }
  },
  mounted() {
    this.findChatList();
  }
};
</script>
<style>
@import "../../assets/css/wechat.css";
</style>


