<template>
  <!--消息列表组件 数据交互频繁-->
  <!--进入 dialogue 页面，携带参数 roomId name group_num -->
  <li :class="{'item-hide':deleteMsg}">
    <!--自定义指令 v-swiper 用于对每个消息进行滑动处理-->
    <router-link
      :to="{ path: '/wechat/dialogue', query: { roomId: item.roomId,roomType:item.roomType,userId:item.user[0].userId,top:item.top,lastMsgId:null,limit:2}}"
      tag="div"
      class="list-info"
      :class="item.top?'top':''"
      v-swiper
      v-on:click.native="toggleMsgRead($event,'enter')"
    >
      <div class="header-box">
        <!--未读并且未屏蔽 才显示新信息数量-->
        <!-- v-show="!read&&!item.quiet"  -->
        <i class="new-msg-count" v-show="item.newMsgCount!==0">{{item.newMsgCount}}</i>
        <!--未读并且屏蔽 只显示小红点-->
        <i class="new-msg-dot" v-show="!read&&item.quiet"></i>
        <!--如果是私聊，只显示一个头像； 如果是群聊，则显示多个头像，flex 控制样式-->
        <!-- 列表头像显示格式 -->
        <!-- <div class="header" :class="[item.roomType=='group'?'multi-header':'']">
         <img v-for="(userInfo,index) in item.user" :src="'/api/file/showImg?fileUrl='+userInfo.headerUrl" />
        </div>-->
        <div class="header" :class="[item.roomType=='group'?'multi-header':'']">
          <van-row v-if="item.user.length==1">
            <van-col span="24" v-for="(userInfo,index) in item.user" :key="index">
              <img
                :src="'/api/file/showImg?fileUrl='+userInfo.headerUrl"
                style="width:48px;height:48px;"
              />
            </van-col>
          </van-row>
          <van-row v-if="item.user.length==2">
            <van-col span="12" v-for="(userInfo,index) in item.user" :key="index">
              <div style="padding:12px 0;">
                <img
                  :src="'/api/file/showImg?fileUrl='+userInfo.headerUrl"
                  style="width:24px;height:24px;"
                />
              </div>
            </van-col>
          </van-row>
          <van-row v-if="item.user.length==3">
            <van-col
              span="12"
              :offset="index==0?6:''"
              v-for="(userInfo,index) in item.user"
              :key="index"
              style="height:24px;"
            >
              <img
                :src="'/api/file/showImg?fileUrl='+userInfo.headerUrl"
                style="width:24px;height:24px;"
              />
            </van-col>
          </van-row>
          <van-row v-if="item.user.length==4">
            <van-col
              span="12"
              v-for="(userInfo,index) in item.user"
              :key="index"
              style="height:24px;"
            >
              <img
                :src="'/api/file/showImg?fileUrl='+userInfo.headerUrl"
                style="width:22px;height:24px;"
              />
            </van-col>
          </van-row>
          <van-row v-if="item.user.length==5">
            <van-col
              span="8"
              :offset="index==0?4:''"
              v-for="(userInfo,index) in item.user"
              style="height:16px;"
              :key="index"
            >
              <div style="padding:6px 0;">
                <img
                  :src="'/api/file/showImg?fileUrl='+userInfo.headerUrl"
                  style="width:16px;height:16px;"
                />
              </div>
            </van-col>
          </van-row>
          <van-row v-if="item.user.length==6">
            <van-col
              span="8"
              v-for="(userInfo,index) in item.user"
              :key="index"
              style="height:16px;"
            >
              <div style="padding:6px 0;">
                <img
                  :src="'/api/file/showImg?fileUrl='+userInfo.headerUrl"
                  style="width:16px;height:16px;"
                />
              </div>
            </van-col>
          </van-row>
          <van-row v-if="item.user.length==7">
            <van-col
              :span="index==0?16:8"
              :offset="index==0?8:''"
              v-for="(userInfo,index) in item.user"
              :key="index"
              style="height:16px;"
            >
              <img
                :src="'/api/file/showImg?fileUrl='+userInfo.headerUrl"
                style="width:16px;height:16px;"
              />
            </van-col>
          </van-row>
          <van-row v-if="item.user.length==8">
            <van-col
              span="8"
              :offset="index==0?4:''"
              v-for="(userInfo,index) in item.user"
              :key="index"
              style="height:16px;"
            >
              <img
                :src="'/api/file/showImg?fileUrl='+userInfo.headerUrl"
                style="width:16px;height:16px;"
              />
            </van-col>
          </van-row>
          <van-row v-if="item.user.length>=9">
            <van-col
              span="8"
              v-for="(userInfo,index) in item.user"
              :key="index"
              style="height:16px;"
            >
              <img
                :src="'/api/file/showImg?fileUrl='+userInfo.headerUrl"
                style="width:16px;height:16px;"
              />
            </van-col>
          </van-row>
        </div>
      </div>
      <div class="desc-box">
        <!--使用过滤器 fmtDate 格式化时间-->
        <div class="desc-time">{{item.msg[item.msg.length-1].time | fmtDate('hh:mm')}}</div>
        <div class="desc-author" v-if="item.roomType=='group'">{{item.roomName}}</div>
        <!--如果没有备注好友，则显示微信昵称-->
        <div class="desc-author" v-else>{{item.user[0].remark||item.user[0].nickname}}</div>
        <div class="desc-msg">
          <div class="desc-mute iconfont icon-mute" v-show="item.quiet"></div>
          <!-- <span v-show="item.roomType=='group'">{{item.msg[item.msg.length-1].name}}:</span> -->
          <span v-show="item.roomType=='group'">{{item.msg[0].user.nickname}}:</span>
          <span>{{item.msg[0].msg}}</span>
          <!-- <span v-if="item.msg[0].msgType == 'img'">[图片]</span> -->
          <!-- <span>{{item.msg[item.msg.length-1].text}}</span> -->
        </div>
      </div>
    </router-link>
    <div class="operate-box">
      <div class="operate-unread" v-if="read" v-on:click="toggleMsgRead">标为未读</div>
      <div class="operate-read" v-else v-on:click="toggleMsgRead">标为已读</div>
      <div class="operate-del" v-on:click="deleteMsgEvent">删除</div>
    </div>
  </li>
</template>
<script>
import qs from "qs";
export default {
  props: ["item"],
  data() {
    return {
      read: this.item.read,
      deleteMsg: false
    };
  },
  mounted() {
    console.log(this.item);
    // 1488117964495
    // fmtDate();
  },
  methods: {
    //切换消息未读/已读状态
    toggleMsgRead(event, status) {
      if (status === "enter") {
        if (this.read) {
          return "";
        }
        this.read = true;
      } else {
        this.read = !this.read;
      }
      if (!this.item.quiet) {
        if (this.read) {
          this.$store.commit("minusNewMsg");
        } else {
          this.$store.commit("addNewMsg");
        }
      }

      event.target.parentNode.parentNode.firstChild.style.marginLeft = 0 + "px";
    },
    //删除列表聊天
    deleteMsgEvent() {
      this.$http
        .post(
          "/api/chat/delChatRoom",
          qs.stringify({ roomId: this.item.roomId })
        )
        .then(res => {
          // console.log(res);
          if (res.data.code == 200) {
            // this.deleteMsg = true;
            this.$parent.findChatList();
          }
        });
      if (!this.item.quiet) {
        if (!this.read) {
          this.$store.commit("minusNewMsg");
        }
      }
    }
  },
  // 参考 https://vuefe.cn/v2/guide/custom-directive.html
  directives: {
    swiper: {
      bind: function(element, binding) {
        var isTouchMove, startTx, startTy;
        element.addEventListener(
          "touchstart",
          function(e) {
            var touches = e.touches[0];
            startTx = touches.clientX;
            startTy = touches.clientY;
            isTouchMove = false;
          },
          false
        );
        element.addEventListener(
          "touchmove",
          function(e) {
            var touches = e.changedTouches[0],
              endTx = touches.clientX,
              endTy = touches.clientY,
              distanceX = startTx - endTx,
              distanceY = startTy - endTy;
            if (distanceX < 0) {
              //右滑
              if (Math.abs(distanceX) >= Math.abs(distanceY)) {
                if (Math.abs(distanceX) > 20) {
                  element.style.transition = "0.3s";
                  element.style.marginLeft = 0 + "px";
                }
              }
            } else {
              //左滑
              if (Math.abs(distanceX) >= Math.abs(distanceY)) {
                if (distanceX < 156 && distanceX > 20) {
                  e.preventDefault();
                  element.style.transition = "0s";
                  element.style.marginLeft = -distanceX + "px";
                  isTouchMove = true;
                }
              }
            }
            // e.preventDefault()
          },
          false
        );
        element.addEventListener(
          "touchend",
          function(e) {
            if (!isTouchMove) {
              return;
            }
            var touches = e.changedTouches[0],
              endTx = touches.clientX,
              endTy = touches.clientY,
              distanceX = startTx - endTx,
              distanceY = startTy - endTy,
              isSwipe = false;
            if (Math.abs(distanceX) >= Math.abs(distanceY)) {
              if (distanceX < 0) {
                return;
              }
              if (Math.abs(distanceX) < 60) {
                isSwipe = true;
                element.style.transition = "0.3s";
                element.style.marginLeft = 0 + "px";
              } else {
                element.style.transition = "0.3s";
                element.style.marginLeft = "-156px";
              }
            }
          },
          false
        );
      }
    }
  }
};
</script>
<style scoped>
.top {
  background-color: #f7f7f7 !important;
}
.img1 {
  width: 48px;
  height: 48px;
}
.img2 {
  width: 24px;
  height: 24px;
}
</style>


